import React from 'react'
import { Button, Col, DatePicker, Form, Input, Radio, Row, Space, theme } from 'antd';


export default function HomeSearchForm({onSubmit}) {
	const { token } = theme.useToken();
	const [form] = Form.useForm();
	const formStyle = {
		maxWidth: 'none',
		background: '#fff',
		borderRadius: token.borderRadiusLG,
		padding: 24,
	};
	const getAge=(birthday)=> {
    var today = new Date();
    var birthDate = new Date(birthday);

    if (today > birthDate) {
        return today.getFullYear() - birthDate.getFullYear();
    } else {
        return today.getFullYear() - birthDate.getFullYear() - 1;
    }
	}
	const onFinish = (values) => {
		console.log(values.birthDay)
		values.age=getAge(values.birthDay)
		onSubmit&&onSubmit(values)
	};
	return (
		<Form form={form} name="advanced_search" style={formStyle} onFinish={onFinish} labelCol={{span:4}} >
			<Row gutter={24}>
				<Col span={8}>
					<Form.Item
						name={`name`}
						label={``}
						rules={[
							{
								required: true,
								message: '',
							},
						]}
					>
						<Input placeholder="" />
					</Form.Item>
				</Col>
				<Col span={8}>
					<Form.Item
						name={`calendarType`}
						label={``}
						rules={[
							{
								required: true,
								message: '',
							},
						]}
					>
						<Radio.Group
							options={[{ label: '', value: '' }, { label: '', value: '' }, { label: '', value: '' }]}
							buttonStyle="solid"
						/>
					</Form.Item>
				</Col>
				<Col span={8}>
					<Form.Item
						name={`birthDay`}
						label={``}
						rules={[
							{
								required: true,
								message: '',
							},
						]}
					>
						<DatePicker showTime />
					</Form.Item>
				</Col>
				<Col span={8}>
					<Form.Item
						name={`sex`}
						label={``}
						rules={[
							{
								required: true,
								message: '',
							},
						]}
					>
						<Radio.Group
							options={[{ label: '', value: '' }, { label: '', value: '' }]}
							value={''}
							buttonStyle='solid'
						/>
					</Form.Item>
				</Col>

				<Col span={8}>
					<Form.Item
						name={`birthPlace`}
						label={``}
						rules={[
							{
								required: true,
								message: '',
							},
						]}
					>
						{/* <Cascader options={PlaceOptions} placeholder='' /> */}
						<Input placeholder="" />
					</Form.Item>
				</Col>

				<Col span={8}>
					<Form.Item
						name={`tel`}
						label={``}
						rules={[
							{
								required: true,
								message: '',
							},
						]}
					>
						<Input placeholder="" />
					</Form.Item>
				</Col>

				<Col span={8}>
					<Form.Item
						name={`askTime`}
						label={``}
					>
						<DatePicker showTime />
					</Form.Item>
				</Col>

			</Row>
			<div style={{ textAlign: 'right' }}>
				<Space size="small">
					<Button type="primary" htmlType="submit">
						
					</Button>
					<Button
						onClick={() => {
							form.resetFields();
						}}
					>
						
					</Button>
				</Space>
			</div>
		</Form>
	);
}
